.row.gutters-tiny {
  margin-right: -6px;
  margin-left: -6px;
}

.row.gutters-tiny > .col,
.row.gutters-tiny > [class*="col-"] {
  padding-left: 6px;
  padding-right: 6px;
}

.row.gutters-tiny .push,
.row.gutters-tiny .block {
  margin-bottom: 12px;
}

.row.row-deck > div {
  display: flex;
  align-items: stretch;
}

.row.row-deck > div > div {
  min-width: 100%;
}

.row.no-gutters {
  margin-right: 0;
  margin-left: 0;
}

.row.no-gutters > .grid,
.row.no-gutters > [class*="grid-"] {
  padding-right: 0;
  padding-left: 0;
}

.grid-22,
.grid-full {
  position: relative;
  width: 100%;
  min-height: 1px;
  padding-right: 10px;
  padding-left: 10px;
}

.grid-22 {
  flex: 0 0 222px;
  max-width: 222px;
}

.grid-full {
  flex-basis: 0;
  flex-grow: 1;
  max-width: 100%;
}

@media (max-width: 991.98px) {
  .grid-22,
  .grid-full {
    flex: 0 0 100%;
    max-width: 100%;
  }
}

